import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatListModule } from '@angular/material/list';
import { NgClass, NgForOf, NgIf } from '@angular/common';
import { coerceBooleanProperty, coerceNumberProperty, NumberInput } from '@angular/cdk/coercion';
import { FormControl, ReactiveFormsModule } from '@angular/forms';

import { debounceTime, distinctUntilChanged, map, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

import { DscInputComponent } from 'sidsc-components/dsc-input';
import { isEmptyArray, isNotEmptyArray, replaceAccents } from 'sidsc-components/core';

import { DscCollapsibleMenu } from './shared/dsc-collapsible-menu';
import { DscMenu } from './shared/dsc-menu';
import { DscSidenavItemComponent } from './dsc-sidenav-item/dsc-sidenav-item.component';
import { Router } from '@angular/router';

export type DscSidenavDrawerMode = 'over' | 'push' | 'side';

@Component({
  selector: 'dsc-sidenav',
  templateUrl: './dsc-sidenav.component.html',
  styleUrls: ['./dsc-sidenav.component.scss'],
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [
    MatSidenavModule,
    MatListModule,
    DscSidenavItemComponent,
    NgForOf,
    NgClass,
    DscInputComponent,
    ReactiveFormsModule,
    NgIf
  ]
})
export class DscSidenavComponent implements OnInit {
  constructor(private _router: Router){}

  private _onDestroy = new Subject<void>();

  @Input()
  customClass: string | string[] | Set<string> | {[p: string]: any} | null | undefined;

  @Input()
  coverSrc?: string;

  private expandedItems: Map<string, boolean> = new Map<string, boolean>();

  @Input() singleExpand: boolean = true;

  @Input()
  get menu(): DscMenu[] | DscCollapsibleMenu[] {
    return this._menu;
  }

  set menu(value: DscMenu[] | DscCollapsibleMenu[]) {
    this._menu = value ? value : [];
    this.menuFiltered = this._menu;
  }

  private _menu!: DscMenu[] | DscCollapsibleMenu[];

  @Input()
  get opened(): boolean {
    return this._opened;
  }

  set opened(value: boolean | string) {
    this._opened = coerceBooleanProperty(value);
  }

  _opened: boolean = true;

  @Input()
  get fixedInViewport(): boolean {
    return this._fixedInViewport;
  }

  set fixedInViewport(value: boolean | string) {
    this._fixedInViewport = coerceBooleanProperty(value);
  }

  _fixedInViewport: boolean = false;

  @Input()
  get fixedTopGap(): number {
    return this._fixedTopGap;
  }
  set fixedTopGap(value: NumberInput) {
    this._fixedTopGap = coerceNumberProperty(value);
  }

  private _fixedTopGap = 0;

  @Input()
  get fixedBottomGap(): number {
    return this._fixedBottomGap;
  }
  set fixedBottomGap(value: NumberInput) {
    this._fixedBottomGap = coerceNumberProperty(value);
  }

  private _fixedBottomGap = 0;

  @Input()
  get mode(): DscSidenavDrawerMode {
    return this._mode;
  }
  set mode(value: DscSidenavDrawerMode) {
    this._mode = value;
  }

  private _mode: DscSidenavDrawerMode = 'side';

  @Input()
  get showFilter(): boolean {
    return this._showFilter;
  }

  set showFilter(value: boolean | string) {
    this._showFilter = coerceBooleanProperty(value);
  }

  _showFilter: boolean = false;

  @Output()
  openedChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Output()
  onClick: EventEmitter<DscMenu> = new EventEmitter<DscMenu>();

  search: FormControl = new FormControl('');

  menuFiltered!: DscMenu[] | DscCollapsibleMenu[];

  private expandedItem?: DscMenu;

  ngOnInit(): void {
    this._filterInit();
    setTimeout(() => this._applyDefaultSelection(),0);
  }

  onExpand(menu: DscMenu): void {
    if (this.singleExpand) {
      this.expandedItems.forEach((_, key) => this.expandedItems.set(key, false));
    }
    this.expandedItems.set(menu.title, true);
  }

  private _applyDefaultSelection(): void {
    if (!this.menu || !this.menu.length) return;

    const path: DscMenu[] = this._findPath(this.menu as DscMenu[], m => !!m.defaultSelected);
    if (!path || !path.length) return;

    const target = path[path.length - 1];

    path.slice(0,-1).forEach(parent => this.expandedItems.set(parent.title, true));

    const parentOfTarget = path.length > 1 ? path[path.length - 2] : undefined;
    if (parentOfTarget) this.onExpand(parentOfTarget);

    if(target.url){
      const current = this._router.url.split('#')[0];
      if (current !== target.url) this._router.navigateByUrl(target.url);
    }

    this.onClick.emit(target);
  }

  onMenuClick(menu: DscMenu): void {
    this.onClick.emit(menu);
  }

  private _findPath(nodes: DscMenu[], predicate: (m: DscMenu) => boolean): DscMenu[]{
    for(const n of nodes){
      if(predicate(n)) return [n];
      if (n.children && n.children.length){
        const childPath = this._findPath(n.children, predicate);
        if (childPath.length) return [n, ...childPath];
      }
    }
    return [];
  }

  isExpanded(menu: DscMenu): boolean {
    return this.expandedItems.get(menu.title) || false;
  }

  private _filterInit(): void {
    this.search.valueChanges
      .pipe(
        map(text => (text && text.length > 3 ? text : '')),
        distinctUntilChanged(),
        debounceTime(450),
        takeUntil(this._onDestroy)
      )
      .subscribe(value => {
        const menuAux = JSON.parse(JSON.stringify(this.menu));
        this.menuFiltered = value ? this._doFilter(menuAux, value) : menuAux;
      });
  }

  private _doFilter(menuList: DscMenu[], value: string): DscCollapsibleMenu[] {
    let menuListFiltered: DscCollapsibleMenu[] = [];

    menuList.forEach((menu: DscMenu) => {
      if (!menu.disabled) {
        if (isEmptyArray(menu.children)) {
          if (this._compare(menu.title, value)) menuListFiltered.push(menu);
        } else {
          const collapsibleMenu = JSON.parse(JSON.stringify(menu));
          collapsibleMenu.children = this._doFilter(collapsibleMenu.children, value);
          collapsibleMenu.expanded = true;
          if (isNotEmptyArray(collapsibleMenu.children)) menuListFiltered.push(collapsibleMenu);
        }
      }
    });

    return menuListFiltered;
  }

  private _compare(s1: string, s2: string): boolean {
    return replaceAccents(s1).trim().toLowerCase().includes(replaceAccents(s2).trim().toLowerCase());
  }
}
