import { Component, EventEmitter, HostBinding, Input, Output, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { Router, RouterLink } from '@angular/router';
import { MatListModule } from '@angular/material/list';
import { NgClass, NgForOf, NgIf } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';

import { Subscription } from 'rxjs';

import { isNotEmptyArray } from 'sidsc-components/core';

import { DscMenu } from '../shared/dsc-menu';
import { DscCollapsibleMenu } from '../shared/dsc-collapsible-menu';

@Component({
  selector: 'dsc-sidenav-item',
  templateUrl: './dsc-sidenav-item.component.html',
  styleUrls: ['./dsc-sidenav-item.component.scss'],
  standalone: true,
  animations: [
    trigger('indicatorRotate', [
      state('collapsed', style({ transform: 'rotate(0deg)' })),
      state('expanded', style({ transform: 'rotate(180deg)' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4,0.0,0.2,1)'))
    ])
  ],
  encapsulation: ViewEncapsulation.None,
  imports: [
    MatListModule,
    RouterLink,
    NgClass,
    MatIconModule,
    NgIf,
    NgForOf
  ]
})
export class DscSidenavItemComponent implements OnInit, OnDestroy {
  protected readonly isNotEmptyArray = isNotEmptyArray;

  private _navSubscription?: Subscription;

  expandedMenu: DscMenu | null = null;

  @Input()
  menu!: DscMenu | DscCollapsibleMenu;

  @Input()
  depth!: number;

  @Output()
  onClick: EventEmitter<DscMenu> = new EventEmitter<DscMenu>();

  @Output() expandChange = new EventEmitter<DscMenu>();

  @Input() expanded: boolean = false;

  @Input() selectedKey: string | null = null;

  @Output() selectedKeyChange = new EventEmitter<DscMenu|null>();

  @HostBinding('attr.aria-expanded')
  ariaExpanded = false;

  constructor(private _router: Router) {
    if (this.depth === undefined) this.depth = 0;
  }

  ngOnInit(): void {
    this._checkIfSelected(this.menu);
    if ('expanded' in this.menu && isNotEmptyArray(this.menu.children)) this._expand(!!this.menu.expanded);
  }

  isActive(menu: DscMenu): boolean {
    if (menu?.url) {
      return this._isSelected(menu.url)
    } else if (menu?.customActiveCondition) {
      return menu.customActiveCondition(menu);
    }
    return false;
  }

  private _checkIfSelected(menu: DscMenu): boolean {
    if (menu.disabled) return false;
    if (menu.url) {
      return this._isSelected(menu.url);
    } else {
      const isSelected = menu.children?.some(child => this._checkIfSelected(child)) as boolean;
      this._expand(isSelected);
      return isSelected;
    }
  }

  private _isSelected(menu: string): boolean {
    const url = this._router.url;
    const menuUrl: string = url.split('#')[0].toString();
    return menu === menuUrl;
  }

  expand(item: DscMenu): void {
    if (!item.disabled) {
      this._expand(!this.expanded);
      this.expandChange.emit(item);
    }
  }

  private _expand(value: boolean): void {
    this.expanded = value;
    this.ariaExpanded = value;
  }

  onExpandChange(item: DscMenu): void {
   this.expandedMenu = this.expandedMenu === item ? null : item;
  }

  onSelected(item: DscMenu): void {
    if (!item.disabled && isNotEmptyArray(item.children)) {
      this.expandChange.emit(item);
    }
  }

  onMenuItemClick(menu: DscMenu): void {
    this.onClick.emit(menu);
  }

  ngOnDestroy(): void {
    if (this._navSubscription) this._navSubscription.unsubscribe();
  }
}
